import React, { Fragment } from 'react'
import ReactDOM from 'react-dom'
import posed from 'react-pose'

import styled from 'styled-components'

const Content = posed.div({
  closed: { height: 0 },
  open: { height: 'auto' },
})

const StyledAccordian = styled.div`
  * {
    margin: 0;
    padding: 0;
  }

  body {
    height: 100vh;
    padding: 0;
    justify-content: center;
    align-items: flex-start;
    display: flex;
    background-image: linear-gradient(-180deg, #ffffff 0%, #f3f3f3 100%);
    padding-top: 20px;
  }

  #root {
    display: flex;
    flex-direction: column;
    width: 400px;
    color: white;
    font-family: 'PT Sans', Helvetica, sans-serif;
  }

  h1 {
    margin-bottom: 20px;
    color: white;
    text-align: center;
  }

  .title {
    cursor: pointer;
    background-image: linear-gradient(-180deg, #ff1c68 0%, #9f0092 300px);
    padding: 5px 8px;
    border-bottom: 1px solid #9f0092;
  }

  .content {
    overflow: hidden;
    font-size: 18px;
    background: rgba(0, 0, 0, 0.8);
  }

  .content-wrapper {
    padding: 5px 8px 20px 8px;
  }
`

const data = [
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `It doesn't matter how much content you put in each accordian. You only have to define one posed component that animates to "auto" and reuse that.`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
]

class Accordian extends React.Component {
  state = { open: false }

  render() {
    const { open } = this.state

    return (
      <StyledAccordian>
        <h1>Accordian demo</h1>
        {data.map(({ title, content }, i) => (
          <Fragment>
            <h2
              className="title"
              onClick={() => this.setState({ open: open === i ? false : i })}
            >
              {open === i ? 'ðŸ¤¯ ' : 'ðŸ™‚ '}
              {title}
            </h2>
            <Content className="content" pose={open === i ? 'open' : 'closed'}>
              <div className="content-wrapper">{content}</div>
            </Content>
          </Fragment>
        ))}
      </StyledAccordian>
    )
  }
}

export default Accordian
